.worker {
  position: absolute;;
  width: 24px;
  height: 24px;
  background-color: red;
  border: 1px solid black;
  z-index: 10;
  cursor:pointer;
  transition: top 0.2s, left 0.2s;
  color: white;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  font-weight: 900;
}

.placesCanGo {
  position: absolute;;
  width: 25px;
  height: 25px;
  background-color: rgb(109, 233, 255);
  opacity: 0.3;
  top: 50px;
  left: 50px;
  cursor: pointer;
  z-index: 11;
}